<template>
<div>
  <v-card>
    <v-card-title>
      My Achievements
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Search"
        single-line
        hide-details
        :loading = "isLoadingSearch"
        loading-text="Loading... Please wait"
      ></v-text-field>
    </v-card-title>
    <v-data-table
      :headers="headers"
      :items="requestList"
      :search="search"
    >
        <template v-slot:item.status="{ item }">
        <v-chip

        class="ma-2"
        color="green"
        x-small
        v-if="item.status==='3' | item.status===3"
        >
          Accepted
        </v-chip>
        <v-chip

        class="ma-2"
        x-small
        v-else-if="item.status==='4' | item.status===4"
        >
            Pending payment
        </v-chip>
        <v-chip

        class="ma-2"
        x-small
        v-else-if="item.status==='5' | item.status===5"
        color="green"

        >
            Payment accepted
        </v-chip>
        <v-chip

        class="ma-2"
        x-small
        v-else-if="item.status==='6' | item.status===6"
        color="green"
        >
            Confirmed service
        </v-chip>
        <v-chip

        class="ma-2"
        x-small
        v-else
        >
            Pending
        </v-chip>
    </template>

    <template v-slot:item.cook_start_date="{ item }">

       <v-btn
            icon
            color="primary"
            @click="view(item)"
        >
            <v-icon>mdi-eye</v-icon>
        </v-btn>
        {{item.cook_start_date}}

    </template>

    <template v-slot:item.title="{ item }">
        <v-chip
        class="ma-2"
        x-small
        >
            {{item.title}}
        </v-chip>
    </template>

    <template v-slot:item.boking_amount="{ item }">
           {{item.alphabetic_code}} {{numberFormat(item.boking_amount)}}
    </template>

    <template v-slot:item.salary="{ item }">
            {{item.alphabetic_code}}  {{numberFormat(item.boking_amount-(item.boking_amount*0.10))}}
    </template>

    <template v-slot:item.order_id="{ item }">
        <router-link :to="'/pub-order/'+item.key" class="pt-3">
            {{item.order_id}}
        </router-link>
    </template>


    </v-data-table>
  </v-card>
  <!--dialog-->
    <viewRequest :viewKey="viewKey" :dialogView.sync="dialogView"/>
  </div>
</template>

<script>
import numeral from 'numeral';
import viewRequest from '../Cart/view.vue';
  export default {
     components: {viewRequest},
    mounted: function () {
      this.getRequests()
    },
    methods: {
       view(viewKey){
           this.dialogView = true;
           this.viewKey = viewKey.key;
       },
      numberFormat(value){
          return numeral(value).format('0,0')
      },

      getRequests (){
            this.axios
              .get('booking/GetMyAchievements')
              .then(
                  response => (
                      this.requestList = response.data,
                      this.isLoadingSearch = !this.isLoadingSearch
                  )
              );
      },

    },
    data () {
      return {
        isLoadingSearch:true,
        viewKey:'',
        dialogView:false,
        allData:[],
        items:[],
        dialogW:false,
        userID:'',
        search: '',
        requestList:[],
        headers: [
        { text: 'OrdeID', value: 'order_id' },
        { text: 'Status', value: 'status' },
          /*{
            text: 'Last Update',
            align: 'start',
            //sortable: false,
            value: 'updated_at',
          },*/
          { text: 'Date', value: 'cook_start_date' },
         // { text: 'Time', value: 'Cook_start_time' },
          { text: 'Amount', value: 'boking_amount' },
          { text: 'Net Salary', value: 'salary' },
          //{ text: 'Type of stove', value: 'type_stove_power_source' },
          //{ text: 'Currency', value: 'alphabetic_code' },
          { text: 'Country', value: 'entity' },
          { text: 'State', value: 'state_name' },
          //{ text: 'Address', value: 'full_address' },
          //{ text: 'Category', value: 'title' },
          //{ text: 'Actions', value: 'actions', sortable: false },
        ],
      }
    },
  }
</script>
